@import url("https://fonts.googleapis.com/css?family=PT+Sans:400,400i,700&display=swap");
@import url("https://fonts.googleapis.com/css?family=PT+Serif:400,400i,700&display=swap");

.container {
  display: flex;
  width: 960px;
  height: 100%;
  margin: 30px auto;
}
.left__column{}
.news {
}
.nav {
  font-family: "PT Sans", sans-serif;
  width: 620px;
  margin: 0 0 10px 0;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  word-spacing: 2px;
  background-color: #d7d8d9;
  font-size: 18px;
  line-height: 19px;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: #333333;
  div {
    padding: 5px 15px;
  }
}
.nav div:hover {
  cursor: pointer;
  color: #ffffff;
  background-color: #333333;
}

.circle-news {
  display: flex;
  flex-direction: row;
  max-width: 620px;
}
.circle-news__img {
  width: 380px;
}
.circle-news__text {
  background-color: #f7f9fa;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  .first {
    font-family: "PT Serif", serif;
    margin: 7px 0 0 0;
    padding: 0 15px 0 15px;
    font-size: 30px;
    line-height: 1.2;
    font-weight: 400;
    letter-spacing: -0.01em;
    color: #c80000;
  }
  .second {
    font-family: "PT Sans", sans-serif;
    max-width: 170px;
    padding: 0 15px 0 15px;
    font-size: 14px;
    line-height: 18px;
    font-weight: 400;
    letter-spacing: -0.01em;
    color: #333333;
  }
  .circle-news__text_arrows {
    font-family: "PT Sans", sans-serif;
    font-weight: 700;
    position: relative;
    top: 40px;
    left: 135px;
    display: flex;
    align-items: center;
    img {
      padding: 4px;
    }
  }
}

.circle-news__redline {
  position: relative;
  background-color: #c70000;
  width: 100%;
  height: 5px;
  z-index: 1;
  margin: -4px 0 0 0;
}

.main-news {
  font-family: "PT Sans", sans-serif;
  width: 620px;
  font-size: 24px;
  line-height: 30px;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: #333333;
}
.main-news__header {
  display: flex;
  align-items: flex-end;
}
.main-news__header_text {
  white-space: nowrap;
}
.main-news__header_line {
  width: 100%;
  border: 1px solid black;
  background-color: #000;
  margin: 0 0 7px 5px;
}
.main-news__content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.main-news__content_item {
  width: 130px;
  padding: 0 10px 0 0;
  font-size: 14px;
  line-height: 18px;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: #333333;
}

.last-news {
  font-family: "PT Sans", sans-serif;
  max-width: 370px;
  font-size: 24px;
  line-height: 30px;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: #333333;
  margin-top: 50px;
}
.last-news__header {
  display: flex;
  align-items: flex-end;
}
.last-news__header_text {
  color: #3d76cc;
  white-space: nowrap;
}
.last-news__header_line {
  width: 100%;
  border: 1px solid #c80000;
  background-color: #c80000;
  margin: 0 0 7px 5px;
}
.last-news__content{}
.last-news__content_item{
font-family: "PT Sans", sans-serif;
font-size: 16px;
line-height: 30px;
font-weight: 400;
letter-spacing: -0.01em;
color: #333333;
li {
  list-style-type: disc;
  list-style-position: inside;
}
a{
  text-decoration: none;
  color: #333333;
}
span{
font-size: 11px;
line-height: 20px;
font-weight: 700;
letter-spacing: -0.01em;
color: #bfbfc0;
padding: 0 0 0 5px;
}
img{
  padding: 0 0 0 4px;
}
}
.last-news__content_item a:hover{
  text-decoration: underline;
  color: #3d76cc;
}

.newsfeed{
  font-family: "PT Sans", sans-serif;
  width: 370px;
  font-size: 24px;
  line-height: 30px;
  font-weight: 400;
  letter-spacing: -0.01em;
  margin-top: 50px;
  color: #333333;
 }
.newsfeeds__header {
  display: flex;
  align-items: flex-end;
 }
.newsfeed__header_text{ 
  color:#3d76cc;
  white-space: nowrap;
}
.newsfeed__header_line{
  width: 100%;
  border: 1px solid #c80000;
  background-color: #c80000;
  margin: 0 0 7px 5px;
 }
.newsfeed__content{
  font-family: "PT Sans", sans-serif;
font-size: 16px;
line-height: 30px;
font-weight: 400;
letter-spacing: -0.01em;
color: #333333;
li {
  list-style-type: disc;
  list-style-position: inside;
}
a{
  text-decoration: none;
  color: #333333;
}
span{
font-size: 11px;
line-height: 20px;
font-weight: 700;
letter-spacing: -0.01em;
color: #bfbfc0;
padding: 0 0 0 5px;
}
img{
  padding: 0 0 0 4px;
}
 }
.newsfeed__content_item_top_news{
  display: flex;
 }
.newsfeed__content_item_top_news_img{ }
.newsfeed__content_item_top_news_text{ 
font-family: "PT Sans", sans-serif;
font-size: 16px;
line-height: 1.2;
font-weight: 700;
letter-spacing: -0.025em;
color: #333333;
padding: 0 0 0 10px;
a{
text-decoration: none;
color: #333333;
}
img{
  padding: 0 0 0 4px;
}
}
.newsfeed__content_item_top_news_text a:hover{
  text-decoration: underline;
  color: #3d76cc;
}
.newsfeed__content_item{
  
 }

.right__column{}
.video {
  font-family: "PT Sans", sans-serif;
  margin: 0 0 0 20px;
}

.video__header {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  span {
    font-size: 18px;
    line-height: 30px;
    font-weight: 700;
    text-transform: uppercase;
    letter-spacing: -0.01em;
    color: #6a0099;
  }
}
.video__header_div {
  height: 2px;
  width: 100%;
  background-color: #000;
}
.video__content {
  display: flex;
  flex-direction: column;
}
.video__content_item {
  display: flex;
  align-items: flex-start;
  padding: 10px 30px 0 0;
  justify-content: flex-start;
  font-size: 14px;
  line-height: 20px;
  font-weight: 700;
  letter-spacing: -0.01em;
  color: #333333;
  img {
    min-width: 140px;
  }
  span {
    padding: 0 0 0 15px;
  }
}

.video__href {
  padding: 10px 0 0 0;
  font-size: 18px;
  line-height: 20px;
  font-weight: 400;
  letter-spacing: -0.01em;
  color: #3d76cc;
}
